















import {Component, Vue} from "vue-property-decorator";

@Component({})
export default class Notification extends Vue {
  isShow = false
  title = ''
  text = ''

  show(title: string, text = ''): void {
    this.text = text
    this.title = title
    this.isShow = true

    setTimeout(() => {
      this.isShow = false
    }, 5000)
  }
}
